


















import { Component, Vue } from 'vue-property-decorator';
import { Action, State } from 'vuex-class';
import { IType } from '@/types/interface';

@Component
export default class SelectionPage extends Vue {
  @Action('getTypeMenu', { namespace: 'testKind' }) getTypeMenu!: () => void;
  @Action('getTypeMenuHot', { namespace: 'testKind' }) getTypeMenuHot!: () => void;
  @Action('resetInformation', { namespace: 'testKind' }) resetInformation!: () => Promise<void>;
  @State('typeMenu', { namespace: 'testKind' }) typeMenu!: IType[];
  @State('typeMenuHot', { namespace: 'testKind' }) typeMenuHot!: IType[];

  created() {
    this.getTypeMenu();
    this.getTypeMenuHot();
  }

  select(item: IType) {
    this.resetInformation()
    .then(() => {
      this.$router.push({
        path: '/main/information/content',
        query: { typename: item.typename, title: item.title },
      });
    });
  }
}
